const { default: styled } = require("styled-components");

const BannerWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: center;
  align-items: center;
`;

const BannerBackgroundWrapper = styled.div`
  @media (min-width: 1140px) {
    flex: 1;
    height: 100%;
  }
`;

const BannerBackground = styled.img`
  width: 100%;
  height: 100%;
  object-fit: contain;
`;

const BannerTextWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  @media (min-width: 1140px) {
    flex: 1;
  }
`;
const BannerText = styled.div`
  font-size: 3.5rem;
  font-weight: 500;
  line-height: 4.5rem;
  max-width: 24rem;
`;
const BannerTextHilight = styled.span`
  font-size: 4.5rem;
  color: var(--primary);
`;

const Home = () => (
  <BannerWrapper id="home">
    <BannerTextWrapper>
      <BannerText>
        Full-Stack <BannerTextHilight>Website Developer</BannerTextHilight>
      </BannerText>
    </BannerTextWrapper>
    <BannerBackgroundWrapper>
      <BannerBackground src="/Banner.jpg" />
    </BannerBackgroundWrapper>
  </BannerWrapper>
);
export default Home;
